    Footer,
    Swiper,
    MainNotice,
    TermPopup2,

    MobileBugoDash,
    SideBarItem,
    MobilePointDash
  },
  props: {},
  data() {
    return {
      secessionPw: "",
      password: "",
      myInfoFlag: false,
      obituary: {
        id: null,
        obituaryName: null,
        obituaryAge: null,
        obituaryAgeYn: null,
        obituarySex: null,
        obituarySexYn: null,
        obituaryMortuary: null,
        obituaryCode: null,
        obituaryDieYn: null,
        obituaryDieDate: null,
        obituaryEnterDate: null,
        obituaryCasketDate: null,
        obituaryCasketYn: null,
        obituaryReligionDate: null,
        obituaryReligionYn: null,
        obituaryEndDate: null,
        obituaryPlace: null,
        obituaryText: null,
        page: {
          page: this.$route.query.page ? this.$route.query.page : 0,
          size: this.$route.query.size ? this.$route.query.size : 8,
          sort: this.$route.query.sort ? this.$route.query.sort : "regDate,desc"
        }
      },
      agreeFlag: false,
      endFlag: this.$route.query.endFlag ? this.$route.query.endFlag : "all",
      searchTxt: this.$route.query.searchTxt ? this.$route.query.searchTxt : "",
      searchType: this.$route.query.searchType
        ? this.$route.query.searchType
        : "name",
      corpSearchTxt: ""
    };
  },
  computed: {
    ...mapGetters([
      "getMainNoticeList",
      "getObituaryCount",
      "getObituaryReadCount",
      "memberInfo"
    ]),
    corpFilterItems() {
      const corpListCopy = JSON.parse(JSON.stringify(this.getCodeList("CORP")));

      return corpListCopy.filter(item => {
        let itemTxt = item.text;
        itemTxt = itemTxt.replace(/ /g, "");
        let corpSearchTxt = this.corpSearchTxt.replace(/ /g, "");
        return itemTxt.toLowerCase().indexOf(corpSearchTxt.toLowerCase()) > -1;
      });
    }
  },
  created() {
    this.$store.dispatch("REQUEST_MAIN");
  },
  mounted() {
    this.$store.commit("END_DEEMED");
    // $document ready 처럼 생성후 실행
    this.secessionPw = "";
    this.$nextTick(function() {
      this.allLoadingEvent();
    });
  },
  methods: {
    adAgreeBtnClickSubmit(val) {
      this.agreeFlag = false;
      this.$store.commit("END_DEEMED");

      const parameter = {
        member: {
          id: this.memberInfo.id,
          memberMarketingAgree: val
        }
      };
      this.$store.dispatch("REQUEST_CHANGE_MARKET_AGREE_MEMBER", parameter);
    },
    adAgreeBtnClick() {
      this.agreeFlag = true;
      this.$store.commit("START_DEEMED");
    },
    popup(path) {
      window.open(`${process.env.VUE_APP_BASEURL}${path}`);
    },

    move(path) {
      this.$router.push(path);
    }
  }
};
</script>
